.s-contacts{
    padding: rem(48) 0 rem(82);
    background-image: url(/img/back/contacts.png);
    background-repeat: no-repeat;
    background-position:0 rem(-100);
    background-size: cover;
    @media (min-width: 768px) {
        padding: rem(40) 0 rem(85);
    }

    @media (min-width: 1024px) {
        padding: rem(93) 0 rem(97);
    }

    @media (min-width: 1600px) {
        background-position:center;
        background-size: cover;
        padding: rem(146) 0 rem(152);
    }
    @media (min-width: 1920px) {
        padding: rem(150) 0 rem(157);
    }
    &__wrapper{
        display: flex;
        flex-wrap:wrap;
        justify-content: space-between;

        margin-bottom: rem(5);
        @media (min-width: 768px) {
            margin-bottom: rem(12);
        }
        @media (min-width: 1024px) {
            margin-bottom: rem(8);
        }
        @media (min-width: 1600px) {
            margin-bottom: rem(7);
        }
        @media (min-width: 1920px) {
            margin-bottom: rem(29);
        }
        &::after{
            content: '* Въезд на территорию складского комплекса «Холмогоры» осуществляется по предварительному заказу пропуска через курирующего менеджера или через наш отдел по работе с клиентами и документами. В обоих случаях для  заказа пропуска необходимо иметь документы, подтверждающие цель приезда. ';
            display: block;
            width: 100%;
            font-weight: 400;
            font-size: rem(9);
            line-height: 1.1;

            display: flex;
            align-items: center;

            color: $grey;
            @media (min-width: 768px) {
                display: none;
            }
        }
    }
    &__item{
        width: 48%;
        @media (min-width: 768px) {
            width: 50%;
        }
        @media (min-width: 1920px) {

        }
        &-title{
            position: relative;
            width: 100%;
            font-weight: 300;
            font-size: rem(21);
            line-height: 1.2;
            color: $grey_light_more_2;
            
            border-bottom: rem(2) solid $red;
            margin-bottom: rem(20);
            padding-bottom: rem(5);
            @media (min-width: 768px) {
                max-width: rem(247);
                font-weight: 400;
                font-size: rem(30);
                line-height: rem(35);
                padding-bottom: rem(10);
            }
            @media (min-width: 1024px) {
                max-width: rem(300);
                font-weight: 300;
                font-size: rem(40);
                line-height: rem(47);
            }
            @media (min-width: 1600px) {
                font-size: rem(50);
                line-height: rem(59);
            }
            @media (min-width: 1920px) {
                max-width: rem(350);

                font-size: rem(60);
                line-height: rem(70);
                padding-bottom: rem(18);
                margin-bottom: rem(12);
            }
        }
        &::after{
            content: '';
            display: block;
            width: rem(2);
            height: rem(20);

            background: $red;
            margin-bottom: rem(2);
            @media (min-width: 768px) {
                display: none;
            }
        }
        &-prefix{
            position: relative;
            display: none;
            min-height: rem(60);
            
            font-weight: 400;
            font-size: rem(10);
            line-height: 120%;

            color: $grey;
            padding-left: rem(10);
            @media (min-width: 768px) {
                display: block;
            }
            @media (min-width: 1024px) {
                font-size: rem(11);
                padding-left: rem(13);
            }
            @media (min-width: 1600px) {
                font-size: rem(12);
                padding-left: rem(15);
            }
            @media (min-width: 1920px) {
                font-size: rem(15);
                padding-left: rem(35);
            }
            &::before{
                content: '';
                position: absolute;
                display: block;
                top: 50%;
                left: 0;
                width: rem(2);
                height: rem(20);
                transform: translateY(-50%);
                background: $red;
                @media (min-width: 1600px) {
                    top: 45%;
                }
                @media (min-width: 1920px) {
                    top: 50%;
                    height: rem(25);
                }
            }
        }
    }
    &__box{
        position: relative;
        border: 1px solid $white;
        padding: rem(25) rem(22);
        margin-bottom: rem(10);
        @media (min-width: 768px) {
            padding: rem(40) rem(63) rem(35) rem(73);
        }
        @media (min-width: 1024px) {
            padding: rem(50) rem(77) rem(51) rem(45);
        }
        @media (min-width: 1600px) {
            padding: rem(50) rem(102) rem(49) rem(45);
            margin-bottom: rem(20);
        }
        @media (min-width: 1920px) {
            margin-bottom: rem(30);
            padding: rem(91) rem(53);
        }
        &-container{
            display: flex;
            align-items: baseline;
            justify-content: baseline;
            margin-bottom: rem(10);
            &:last-child{
                margin: 0;
            }
            &:hover{
                .s-contacts__box-text{
                    color: $grey_light_more_2;
                }
            }
        }
        &-subtitle{
            min-width: rem(64);
            text-align: right;
            font-weight: 300;
            font-size: rem(15);
            line-height: 1.4;

            color: $grey_light;
            @media (min-width: 768px) {
                min-width: rem(90);
                font-weight: 300;
                font-size: rem(21);
            }
            @media (min-width: 1024px) {
                min-width: rem(120);
                font-size: rem(28);
            }
            @media (min-width: 1600px) {
                min-width: rem(135);
                font-size: rem(32);
            }
            @media (min-width: 1920px) {
                min-width: rem(170);
                font-size: rem(40);
            }
        }
        &-text{
            font-weight: 400;
            font-size: rem(15);
            line-height: 1;
            letter-spacing: -0.03em;
            color: #9A9EA9;

            margin-left: rem(18);
            @media (min-width: 768px) {
                font-size: rem(21);
                margin-left: rem(30);
            }
            @media (min-width: 1024px) {
                font-size: rem(28);
                margin-left: rem(30);
            }
            @media (min-width: 1600px) {
                font-size: rem(32);
                margin-left: rem(22);
            }
            @media (min-width: 1920px) {
                font-size: rem(40);
                margin-left: rem(40);
            }
        }
        &-popup{
            position: absolute;
            bottom:rem(10);
            right: rem(10);

            font-weight: 400;
            font-size: rem(8);
            line-height: 170%;

            color: $grey_light_more_2;
            @media (min-width: 768px) {
                bottom:rem(40);
                right: rem(25);
            }
            @media (min-width: 1024px) {
                font-size: rem(13);
            }
            @media (min-width: 1920px) {
                font-size: rem(15);
                bottom:rem(40);
                right: rem(50);
            }
        }
    }
    &__information{
        margin-bottom: rem(56);
        display: block;
        gap: rem(17);
        @media (min-width: 768px) {
            display: flex;
            align-items: center;
            gap: rem(30);
        }
        @media (min-width: 1024px) {
            gap: rem(48);
            margin-bottom: rem(93);
        }
        @media (min-width: 1600px) {
            gap: rem(45);
            margin-bottom: rem(118);
        }
        @media (min-width: 1920px) {
            justify-content: space-between;
        }
        &-item{
            display: inline-flex;
            font-weight: 300;
            font-size: rem(10);
            line-height: 1.2;
            color: #6B7081;
            margin-right: rem(17);
            span{
                font-weight: 400;
            }
            &:last-child{
                margin-right: 0;
            }
            @media (min-width: 1024px) {
                font-size: rem(12);
            }
            @media (min-width: 1600px) {
                font-size: rem(13);
            }
        }
    }
    &__driving{
        @media (min-width: 768px) {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: space-between;
        }
        @media (min-width: 1920px) {
            max-width: rem(995);
        }
        &-title{
            display: inline-block;
            font-weight: 400;
            font-size: rem(21);
            line-height: rem(25);

            color: $grey_light_more_2;
            
            border-bottom: 2px solid $red;
            padding-bottom: rem(5);
            margin-bottom: rem(5);
            @media (min-width: 768px) {
                max-width: rem(287);

                font-weight: 400;
                font-size: rem(30);
                line-height: rem(35);

                padding-bottom: rem(10);
            }
            @media (min-width: 1024px) {
                font-size: rem(30);
                padding-bottom: rem(21);
            }
            @media (min-width: 1600px) {
                font-size: rem(40);
                line-height: rem(47);
                padding-bottom: rem(20);
            }
        }
        &-prefix{
            font-weight: 400;
            font-size: rem(9);
            line-height: rem(11);

            color: $grey_light_more_2;
            margin-bottom: rem(20);
            @media (min-width: 768px) {
                max-width: rem(340);
                margin-bottom: rem(0);
            }
            @media (min-width: 1024px) {
                font-size: rem(13);
                line-height: rem(15);
                max-width: rem(460);
                margin-right: auto;
                margin-left: rem(88);
            }
            @media (min-width: 1600px) {
                
                margin-left: rem(87);
            }
            @media (min-width: 1920px) {
                font-size: rem(15);
                max-width: rem(560);
                margin-right: 0;
                margin-left: rem(0);
            }
        }
        &-location{
            width: 100%;
            @media (min-width: 768px) {
                margin-top:rem(102)
            }
            @media (min-width: 1024px) {
                margin-top:rem(53)
            }
            @media (min-width: 1920px) {
                margin-top:rem(50)
            }
        }
    }
    &__location{
        &-item{
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: rem(23);
            @media (min-width: 768px) {
                justify-content:left;
                margin-bottom: rem(20);
            }
            @media (min-width: 1024px) {
                justify-content:space-between;
                margin-bottom: rem(33);
            }
            @media (min-width: 1024px) {
                margin-bottom: rem(40);
            }
            @media (min-width: 1920px) {
                margin-bottom: rem(37);
            }
        }
        &-title{
            font-weight: 400;
            font-size: rem(15);
            line-height: rem(18);

            color: #9A9EA9;
            @media (min-width: 768px) {
                min-width: rem(273);
                font-size: rem(17);
                line-height: rem(20);
            }
            @media (min-width: 1600px) {
                font-size: rem(19);
                line-height: rem(22);
            }
        }
        &-btn{
            display: flex;
            align-items: center;
            @media (min-width: 768px) {
               margin-left: rem(97);    
            }
            @media (min-width: 1024px) {
                margin-left: rem(375);
                margin-right: auto;
            }
            @media (min-width: 1600px) {
                
            }
            @media (min-width: 1920px) {
                margin-right: auto;
                margin-left: rem(360);
            }
            svg{
                font-size: rem(25);
                @media (min-width: 768px) {
                    font-size: rem(30);
                }
                @media (min-width: 1600px) {
                    font-size: rem(40);
                }
            }
        }
        &-size{
            font-weight: 400;
            font-size: rem(15);
            line-height: rem(18);

            color: $grey_light;
            margin:0 rem(15) 0 rem(30);
            @media (min-width: 768px) {
                font-size: rem(17);  
                line-height: rem(20);
                margin: 0;
                margin-left: rem(25);
            }
            @media (min-width: 1600px) {
                font-size: rem(21);  
                line-height: rem(25);
            }
            @media (min-width: 1920px) {
                margin-left: rem(30);
            }
        }
    }
}

